"use client";

import React, {useState} from 'react'
import {AuthButton, FormLine} from "@/components/FormComponents";

const SignInPage = () => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [email, setEmail] = useState('');
    const [isSignUp, setIsSignUp] = useState(true);

    const handleSubmit = (e: any) => {
        e.preventDefault();
        console.log({username, password});
    }

    const resetInfor = () => {
        setUsername("");
        setPassword("");
        setEmail("");
    }

    const handleChangeStatus = () => {
        setIsSignUp(!isSignUp);
        resetInfor();
    }

    const userIcon = '/userIcon.png';
    const passwordIcon = '/passwordIcon.png';
    const emailIcon = '/emailIcon.png';

    return (
        <div className="flex-center flex-1 w-screen h-screen">
            <div className="flex flex-row w-2/3 h-2/3 rounded-lg border-2 border-dark-400 relative hover:shadow-[0_0_15px_0_#54BBF2] shadow-dark-400">

                {/*Sign In Form*/}
                <div className={`absolute w-1/2 flex-col h-full flex-center space-y-10 transition-opacity duration-1000 left-0 ${isSignUp ? "opacity-0 w-0 pointer-events-none" : "opacity-100 w-1/2"}`}>

                    <h2 className="text-4xl">Sign in</h2>

                    <form onSubmit={handleSubmit} className="space-y-8 w-2/3">
                        {/*Username */}
                        <FormLine
                            placeholder="Username"
                            value={username}
                            onChangeFunc={(e) => setUsername(e.target.value)}
                            icon={userIcon}
                            labelShown={username == ""}
                        />

                        {/*Password*/}
                        <FormLine
                            placeholder="Password"
                            value={password}
                            onChangeFunc={(e) => setPassword(e.target.value)}
                            icon={passwordIcon}
                            labelShown={password == ""}
                        />

                        {/*Button*/}
                        <AuthButton
                            authChoice="Sign in"
                        />
                    </form>

                    <div className="flex-center flex-col space-y-2">
                        <p className="text-sm">Don't have an account?</p>
                        <button
                            type="button"
                            onClick={handleChangeStatus}
                            className="text-dark-400 hover:underline cursor-pointer font-bold text-sm"
                        >Sign up</button>
                    </div>
                </div>

                {/*Blue Sliding Panel*/}
                <div className={`flex relative flex-row items-center w-1/2 h-full bg-dark-400 rounded-md transition-transform duration-1000 ease-in-out ${isSignUp ? "translate-x-0 left-0" : "translate-x-full right-0"}`}>
                    <div className={`absolute mx-10 w-1/2 transition-opacity duration-1000 ease-in-out text-left left-0 ${isSignUp ? "opacity-100" : "opacity-0"}`}>
                        <h2>{"WELCOME!"}</h2>
                        <p>{"We are delighted to have you here"}</p>
                    </div>

                    <div className={`absolute mx-10 w-1/2 transition-opacity duration-1000 ease-in-out text-right right-0 ${isSignUp ? "opacity-0" : "opacity-100"}`}>
                        <h2>{"WELCOME BACK!"}</h2>
                        <p>{"We are happy to have you with this again"}</p>
                    </div>

                </div>

                {/*Sign Up Form */}
                <div className={`absolute flex-col w-1/2 h-full flex-center space-y-10 transition-opacity duration-1000 left-1/2 ${isSignUp ? "opacity-100 w-1/2" : "opacity-0 w-0 pointer-events-none"}`}>

                    <h2 className="text-4xl">Sign up</h2>

                    <form onSubmit={handleSubmit} className="space-y-8 w-2/3">
                        {/*Username */}
                        <FormLine
                            placeholder="Username"
                            value={username}
                            onChangeFunc={(e) => setUsername(e.target.value)}
                            icon={userIcon}
                            labelShown={username == ""}
                        />

                        {/*Email*/}
                        <FormLine
                            placeholder="Email"
                            value={email}
                            onChangeFunc={(e) => setEmail(e.target.value)}
                            icon={emailIcon}
                            labelShown={email == ""}
                        />

                        {/*Password*/}
                        <FormLine
                            placeholder="Password"
                            value={password}
                            onChangeFunc={(e) => setPassword(e.target.value)}
                            icon={passwordIcon}
                            labelShown={password == ""}
                        />

                        {/*Button*/}
                        <AuthButton
                            authChoice="Sign up"
                        />
                    </form>

                    <div className="flex-center flex-col space-y-2">
                        <p className="text-sm">Don't have an account?</p>
                        <button
                            type="button"
                            onClick={handleChangeStatus}
                            className="text-dark-400 hover:underline cursor-pointer font-bold text-sm"
                        >Sign in</button>
                    </div>

                </div>
            </div>

        </div>
    )
}
export default SignInPage;


